<script>
import layer from '@/utils/layer';
import Delconfirm from '@components/Delconfirm.vue';
import api from '@/api';

export default {
  name: 'setting',
  components: {
    Delconfirm,
  },
  data() {
    return {
      ratelimit: '',
      pertotal: '',
      project_id: null,
      project_name: null,
      project_secret: null,
      urls: null,
      EndpointType: '',
    };
  },
  methods: {
    copyId(id, type) {
      const cInput = document.createElement('input');
      cInput.value = id;
      document.body.appendChild(cInput);
      cInput.select();
      document.execCommand('Copy');

      layer.tip(`${type} copied to clipboard.`);
    },
    delProject() {
      this.$refs.delconfirm.addProject();
    },
    saveChanges() {
      if (!this.project_name.length) {
        return;
      }
      const obj = {
        project_id: this.project_id,
        project_name: this.project_name,
      };
      api.projectApi.change_project(obj).then((result) => {
        if (!result.success) {
          layer.tip(result.msg, 'error');
        } else {
          layer.tip("change project's name successfully !");
          // setTimeout(()=>{
          //   this.cancelCreate();
          //   // this.$router.go(0)
          //   this.$router.push('/dashboard/heco')
          // },1000)
        }
      });
    },
  },
  computed: {
    selectedUrl() {
      if (!this.EndpointType) {
        return;
      }
      let arr = [];
      this.urls.map((item) => {
        if (item.name === this.EndpointType) {
          arr = item.urls;
        }
      });
      return arr;
    },
  },
  mounted() {
    const {
      project_id, project_name, project_secret, urls,
    } = this.$route.query.item;
    this.project_id = project_id;
    this.project_name = project_name;
    this.project_secret = project_secret;
    this.urls = urls;
    this.EndpointType = urls && urls[0] ? urls[0].name : '';
  },
};
</script>

